@import url(https://fonts.googleapis.com/css?family=Ubuntu&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  width: 100%;
  background: #FBCF62;
}

html{
  position: relative;
  min-height: 100%;
}
.Header {
    background-color: #3E3E44;
    display: flex;
    justify-content: space-between;
}
.Header a{
    text-decoration: none;
}
.Header h1 {
    color: floralwhite;
    margin: 0;
    margin-left: 65px;
    font-family: cursive;
}
.Header input{
    position: absolute;
    left: 200px;
    top: 15px;
    border-radius: 10px;
    border: none;
    height: 20px;
    text-indent: 10px;
    outline: none;
}
.Header button{
    position: absolute;
    left: 298px;
    top: 15px;
    border-radius: 0px 10px 10px 0px;
    border: none;
    background-color: #FBCF62;
    height: 22px;
    outline: none;
}
.Menu {
    display: flex;
    font-size: 30px;
}
.Input {
    border-radius: 30px;
}

.MenuItem {
    display: flex;
    margin-right: 10px;
    text-decoration: none;
    color: floralwhite;
    font-family: cursive;
}
.MenuItem:hover{
    color: dimgrey ;
}
.Footer{
    position: absolute;
    background-color: #3E3E44;
    width: 100%;
    bottom: 0;
    height: auto;
}
.Footer p{
    margin-left: 20px;
}
.Sidebar {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #3E3E44;
    justify-content: space-evenly;
    margin-top: -20px;
    width: 200px;
    height: 600px;
}

.SidebarItem a{
    text-decoration: none;
    margin-top: 20px;
    margin-left: 15px;
    font-family: monospace;
    font-size: 15px;
    color: floralwhite;
}
.SidebarItem:hover {
    color: gray;
}

.SocialMedia img{
    width: 30px;
}
.SocialMediaLeftBar{
    display: flex;
    flex-direction: row;
}
.MainText{
    display: flex;
    flex-direction: column;
    align-items: center;
    line-height: 0.1;
    position: absolute;
    left: 600px;
    top: 360px;
    font-size: 25px;
    font-style: italic;
    color: floralwhite;
    font-family: 'Ubuntu', sans-serif;
}
.Main h5{
    opacity: 0.7;
}
.Main > div > img {
    position: absolute;

}

.balloon{
    -webkit-transform: rotate(15deg);
            transform: rotate(15deg);
    width: 130px;
    top: 80px;
    left: 705px;
}
.hat{
    -webkit-transform: rotate(-15deg);
            transform: rotate(-15deg);
    width: 90px;
    top: 105px;
    left: 725px;
}
